<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <button @click="fn">按钮</button>
    <h1>VUEX</h1>
    <button @click='increment'>+1</button>
    <button @click='add'>+1</button>
    <span>{{count}}</span>
    <button @click='getData'>获取轮播数据</button>
  </div>
</template>

<script>
import {mapGetters,mapActions,mapMutations} from 'vuex'
import {getSliders} from '@api/index.js'  //在build/webpack.base.conf.js 配置路径
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  computed:{
    ...mapGetters([
      'count'
    ])
  },
    methods:{
      ...mapActions([
        'increment'
        ]),

      ...mapMutations({
        'add':'INCREMENT' //改名
      }),

      fn(){
        console.log($('h1'))
        //this.$notify()//默认值
        //this.$notify('默认延时3s');
        this.$notify('自定义提示',{delay:1000});
      },

      async getData(){ //异步获取数据
        let sliders=await getSliders()
        console.log(sliders)
      }
    }
}
</script>
<style scoped></style>



